.alignCenter{
align-items: center;
justify-content: center;
display: flex;
}

.separator{
  display: flex;
  align-items: center;
}

/* Spacing */
.separator h1{
  padding: 0 2rem;
}

.separator .line{
  flex: 1;
  height: 2px;
  background-color: white;
}


.fade-enter {
  opacity: 0;
  transform: translateX(-100%);
}
.fade-enter-active {
  opacity: 1;
  transform: translateX(0%);
}
.fade-exit {
  opacity: 1;
  transform: translateX(0%);
}
.fade-exit-active {
  opacity: 0;
  transform: translateX(100%);
}
.fade-enter-active,
.fade-exit-active {
  transition: opacity 500ms, transform 500ms;
}
